import React, { useRef, useEffect, useState } from "react";
import CookieConsent from "react-cookie-consent";
import { Link } from "react-router-dom";
import axios from "axios";
function GDPRPopup() {
  var apiUrl = "https://backend.amourette.no/api/notifications/";
  const [IpAddress, setIpAddress] = useState("");
  useEffect(() => {
    // Fetch IP address from ipify API
    const getIpAddress = async () => {
      try {
        const response = await axios.get("https://api.ipify.org?format=json");

        setIpAddress(response.data.ip); // Update state with IP address
      } catch (error) {
        console.error("Error fetching IP address:", error);
      }
    };

    getIpAddress();
  }, []);
  const handleCookies = async (status) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
      consent_status: status,
      ip_address: IpAddress,
    };
    try {
      const res = await axios.post(apiUrl + "setgdprCookies", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setConsentCookie(status);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const setConsentCookie = (status) => {
    // Get current date and time
    const now = new Date();
    // Set expiration time to 12 minutes from now
    now.setTime(now.getTime() + 12 * 60 * 1000); // 12 minutes in milliseconds
    const expires = "expires=" + now.toUTCString();

    // Set the cookie with the specified status and expiration
    //document.cookie = `gdprConsent=${status}; ${expires}; path=/`;

    // Automatically remove the gdprConsent cookie after it expires
    setTimeout(() => {
      const cookie = document.cookie
        .split("; ")
        .find((row) => row.startsWith("gdprConsent="));
    }, 12 * 60 * 1000); // 12 minutes in milliseconds (timeout to expire the cookie)
  };

  return (
    <CookieConsent
      location="bottom" // Position of the popup (bottom, top, etc.)
      buttonText="Accept All" // Text for the consent button
      declineButtonText="Decline" // Text for the decline button
      enableDeclineButton // Enable a "Decline" button
      onAccept={() => handleCookies("Accept")}
      onDecline={() => handleCookies("Decline")}
      cookieName="gdprConsent" // Name of the cookie to track consent
      style={{ background: "#000", color: "#fff" }} // Styling for the popup
      buttonStyle={{
        background: "#4CAF50",
        color: "#fff",
        fontSize: "13px",
      }} // Styling for the Accept button
      declineButtonStyle={{
        background: "#d32f2f",
        color: "#fff",
        fontSize: "13px",
      }} // Styling for the Decline button
      expires={365} // Days until the consent expires
    >
      <p style={{ fontSize: "14px", margin: "0" }}>
        We use cookies to improve your experience on our site. You can read our{" "}
        <Link
          to="/privacy-policy"
          style={{ color: "#4CAF50", textDecoration: "underline" }}
        >
          Privacy Policy
        </Link>{" "}
        to learn more.
      </p>
    </CookieConsent>
  );
}

export default GDPRPopup;
